article {
  max-width: 750px;
  margin: 100px auto;
}

.btn {
  border: 1px solid #40d3ad;
  border-radius: 50px;
  display: inline-block;
  font-size: 15px;
  font-family: "Roboto Regular", sans-serif;
  padding: 26px 62px;
  text-decoration: none;
  background: #40d3ad;
  color: #fff;
  cursor: pointer;
}

.btn:hover {
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
  background: linear-gradient(to bottom, #fcfff4, #e9e9ce);
  color: #40d3ad;
}

