@font-face {
  font-family: "Roboto Regular";
  src: url("font/Roboto-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Roboto Bold";
  src: url("font/Roboto-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Roboto Medium";
  src: url("font/Roboto-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@media screen and (max-width: 2550px) {
  .bg-txt {
    margin: 40px 0 -130px -60px;
  }

  .menu__icon {
    display: inline-block;
  }

  .menu__links {
    position: fixed;
    display: block;
    top: 0;
    right: 0;
    left: 0;
    margin-top: 60px;
    background-color: #fff;
    z-index: 1000;
    /*overflow: auto;*/
    opacity: 0;
  }

  .menu__links-item {
    display: block;
    padding: 80px 0;
    text-align: center;
    color: #000;
  }

  .item-top {
    margin-top: 90px;
  }

  .item-bottom {
    margin-bottom: 5px;
  }
}

@media screen and (max-width: 1920px) {
  .bg-txt {
    margin: 0 0 -130px -60px;
  }

  .text-bg-pict {
    display: flex;
    position: absolute;
    top: 30%;
    left: 15%;
  }

  .menu__links-item {
    padding: 40px 0;
  }

  .item-top {
    margin-top: 0;
  }
}

@media screen and (max-width: 1024px) {
  .bg-txt {
    margin: -150px 0 -230px -60px;
  }

  .text-bg-pict {
    display: flex;
    position: absolute;
    top: 20%;
    left: 5%;
  }

  .menu__links-item {
    font-size: 40px;
    padding: 20px 0;
  }
}

